
.home_wrap {
	padding-bottom: 50px;
}
/*    swipper   */
.swiper img {
	width: 100%;
}
/*  首页内容   */
.list {
	text-align: center;
	overflow: hidden;
	list-style: none;
	margin: 0;
	padding: 0;
	background: white;
}
.list li {
	width: 33.3%;
	float: left;
	border: 1px solid #E7E7E7;
	height: 122px;
	padding-top: 30px;
	font-size: 14px;
	padding-bottom: 50px;
}
.list img {
	height: 30px;
}
.list p {
	margin: 0;
	margin: 10px auto;
}
.list li span {
	display: inline-block;
	width: 16px;
	height: 16px;
	border-radius: 50%;
	line-height: 16px;
	color: #FFFFFF;
	font-size: 12px;
}
.home_fir_li {
	position: relative;
}
.home_fir_p {
	position: absolute;
	top: 0px;
	left: calc(16vw - 6px);
	text-align: center;
}



